import React, { useState } from "react";
import style from "./Form.module.css";


const Form = () => {
  const [submittedForm, setSubmittedForm] = useState();
  function onSubmit(e) {
    e.preventDefault();
    const data = new FormData(e.target);
    setSubmittedForm(data);
  };

  return (
    <div className = {style.form}>
      {
        submittedForm ? (
          <div><p>Here is your request summary:</p> 
          Name:{submittedForm.get("myLName")} {submittedForm.get("myFName")} and {submittedForm.get("myEmail")} {submittedForm.get("myPhone")}</div>
        ) : (
          <form onSubmit={onSubmit} >
            <p className ={style.formtitle}> Any Questions: </p>
            <label htmlFor="myFNameId">First Name: </label><br/>
            <input type="fname" name="myFName" id="myFNameId"/><br/><br/>
            <label htmlFor="myLNameId">Last Name: </label><br/>
            <input type="lname" name="myLName" id="myLNameId"/><br/><br/>
            <label htmlFor="myEamilId">E-mail</label><br/>
            <input type="email" name="myEmail" id="myEamilId"/><br/><br/>
            <label htmlFor="myPhoneId">Phone Number</label><br/>
            <input type="tel" name="myPhone" id="myPhoneId"/><br/><br/>
            <label htmlFor="myneuId">Are you currently studying at Northeastern University?</label><br/>
            <select name="myneu" id="myneuId">
              <option value="Yes">Yes</option>
              <option value="No">No</option>
              </select><br/><br/>
            <label htmlFor="myMajorId">Your Major</label><br/>
            <select name="myMajor" id="myMajorId">
              <option value="Information System">MSIS</option>
              <option value="Computer Science">MSCS</option>
              <option value="Data Analytics Engineering">DAE</option>
              <option value="Other">Other</option>
            </select><br/><br/>
            <input type="checkbox" id="agreeid" name="myAgree" />
            <label htmlFor= "agreeid"> I agree to recieve further information from this website.</label><br/><br/>
            <label htmlFor="questionId">Comment: </label><br/>
            <textarea  name="myquestion" id="questionId"/><br/><br/>
            <input type="submit" value="Send it" /><br /><br/>
            
          </form>
        )
      }
    </div>
  )
}

export default Form
